html{
    scroll-behavior: smooth;
    scroll-padding-top: 20vh;
}

.header_office{
    padding: 11px 25px;
    background-color: black;
    color: #fff;
    position: fixed;  
    top: 0;  
    z-index: 100;  
    width: 100%; 
}

.header_row{
    display: flex;
    column-gap: 25px;
    align-items: center;
    justify-content: space-between;
}

.header_logo{
    display: flex;
    align-items: center;
    max-width: 90px;
    max-height: auto;

}

.header_nav_office ul{
    display: flex;
    column-gap: 20px;
    align-items: center;
    
}


.header_nav_office {
   font-weight: 400;
   font-size: 13px;
   line-height: 1.5;
   letter-spacing: 0.01em;

}

.header_nav_office a{
    color: #d3d3d3;
    transition: filter 0.2s ease;
}

a.header_btn{
    padding: 10px;
    border-radius: 8px;
    background-color: #F0F712;
    color: #000;
    transition: filter 0.2s ease;
}

a.header_btn:active{
    filter: brightness(70%);
}

.header_nav a:active{
    filter: brightness(70%);
}

.header_user {
    font-size: 30px;
}

.header_user_name{
    font-size: 16px;
}


.profile-container2 {
    position: relative;
    width: 45px;
    height: 45px;
}

.profile-image2 {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}
